import { Switch, Route } from 'react-router-dom'
import About from './About'
import Contact from './Contact'
import Home from './Home'

const Routes = () => {
    return (<Switch>
        <Route path="/about">
            <About />
        </Route>
        <Route path="/contact">
            <Contact />
        </Route>
        <Route path="/">
            <Home />
        </Route>
    </Switch>);
}

export default Routes;